import React from 'react'
import PropTypes from 'prop-types'
import { graphql, useStaticQuery } from 'gatsby'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import withStyles from '@material-ui/styles/withStyles'
import SectionHeader from '../../components/sections-header/section-header'
import ProfileCard from '../../components/profile-card/profile-card'

const withStyling = withStyles(theme => ({
  root: {
    ...theme.base.section.root,
  },
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2, 0),
    marginTop: theme.spacing(2),
    justifyContent: 'center',
  },
}))

const AboutUs = ({ classes }) => {
  const { data } = useStaticQuery(graphql`
    {
      data: allSectionsJson(filter: { id: { eq: "about-us" } }) {
        nodes {
          id
          title
          desc
          members {
            name
            image
            roles
            skills
          }
        }
      }
    }
  `)
  const sectionDetails = data.nodes[0]
  const teamMembers = sectionDetails.members

  return (
    <Container className={classes.root}>
      <SectionHeader title={sectionDetails.title} />
      <Grid
        container
        direction={'row'}
        justify={'space-between'}
        alignItems={'stretch'}
        className={classes.content}
        spacing={5}
        itemScope={true}
        itemType={'https://schema.org/Organization'}
      >
        {teamMembers.map((teamMember, index) => (
          <Grid
            key={index}
            item
            sm={12}
            md={6}
            lg={4}
            style={{ width: '100%' }}
            itemProp={'employee'}
          >
            <ProfileCard
              name={teamMember.name}
              photo={teamMember.image}
              roles={teamMember.roles}
              skills={teamMember.skills}
            />
          </Grid>
        ))}
      </Grid>
    </Container>
  )
}

AboutUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyling(AboutUs)
