import React from 'react'
import PropTypes from 'prop-types'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Chip from '@material-ui/core/Chip'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import withWidth, { isWidthDown } from '@material-ui/core/withWidth'

import capitalize from '../../lib/capitalize'
import technologies from '../../lib/technologies'

const TechnologiesCategoryList = ({ current, categories, callback, morph }) => {
  let listStyle = {}
  let listItemStyle = { textAlign: 'right' }

  if (morph) {
    listStyle = {
      ...listStyle,
      display: 'flex',
      flexWrap: 'wrap',
    }
    listItemStyle = {
      ...listItemStyle,
      textAlign: 'center',
      width: '100%',
    }
  }

  return (
    <List dense={true} style={listStyle}>
      {categories.map((category, index) => (
        <ListItem
          key={index}
          button
          style={listItemStyle}
          selected={current === category}
          onClick={() => callback(category)}
        >
          <ListItemText primary={capitalize(category)} />
        </ListItem>
      ))}
    </List>
  )
}

TechnologiesCategoryList.propTypes = {
  current: PropTypes.string.isRequired,
  categories: PropTypes.array.isRequired,
  callback: PropTypes.func.isRequired,
  minimize: PropTypes.bool,
}

const TechnologyList = ({ list, selected }) => (
  <Grid container spacing={1}>
    {list.map((item, index) => {
      const isSelected = item.categories.indexOf(selected) >= 0
      return (
        <Grid key={index} item>
          <TechnologyListItem
            label={item.label}
            categories={item.categories}
            selected={isSelected}
          />
        </Grid>
      )
    })}
  </Grid>
)

TechnologyList.propTypes = {
  list: PropTypes.array.isRequired,
  selected: PropTypes.string,
}

const TechnologyListItem = ({ label, selected }) => (
  <Chip label={label} color={selected ? 'primary' : 'default'} />
)

TechnologyListItem.propTypes = {
  label: PropTypes.string.isRequired,
  selected: PropTypes.bool.isRequired,
}

const TechnologiesCard = ({ width }) => {
  const possibleTechnologies = technologies.reduce(
    (acc, { categories }) => ({
      ...acc,
      ...categories.reduce((cats, c) => ({ ...cats, [c]: false }), {}),
    }),
    {}
  )
  const categories = Object.keys(possibleTechnologies).sort()
  const [category, setCategory] = React.useState(categories[0])
  const isMorphed = isWidthDown('sm', width)

  return (
    <Container disableGutters>
      <Card raised={true}>
        <CardContent>
          <Grid container direction={'row'} spacing={2}>
            <Grid item sm={12} md={3}>
              <Typography variant={'h5'} color={'primary'}>
                Technologien
              </Typography>
              <TechnologiesCategoryList
                current={category}
                categories={categories}
                callback={setCategory}
                morph={isMorphed}
              />
            </Grid>
            <Grid item sm={12} md={9}>
              <TechnologyList list={technologies} selected={category} />
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Container>
  )
}

TechnologiesCard.propTypes = {
  width: PropTypes.string.isRequired, // breakpoint string from MUI
}

export default withWidth()(TechnologiesCard)
